/* ------------------------------New CSS file---------------------------------- */

@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@100;300;400;500&display=swap');

:root{
  font-size: 16px;
  font-weight: 600;
  --primary-color:white;
  --secondary-color: #f8f8f8;
}

*,
*::after,
*::before{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body{
  font-family: 'Poppins', sans-serif;
  min-width: 16rem;
  overflow-x: hidden;
 overflow-y: auto;
}


/* width */
::-webkit-scrollbar {
  width: 0.5rem;
  cursor: pointer;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #888;
  border-radius: 1rem;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}

















